@charset "UTF-8";

.container {
  position: relative;
}

#cart {
  background-color: #ffffff;
  border-color: black;
  border-style: solid;
  border-width: 5px;
  border-radius: 40px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 2;
  padding: 10px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
}

#cart-image {
  background-image: url("../../../public/images/item/cart.png");
  background-size: 100px 100px;
  width: 100px;
  height: 100px;
}

#cart-number {
  font-size: 30px;
  color: white;
  font-weight: bold;
  width: 50px;
  height: 50px;
  border-radius: 30px;
  background-color: red;
  border: none;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
}

@font-face {
  font-family: "HuiFont";
  src: url("../../../public/fonts/HuiFontP29.ttf");
}

.item-name {
  font-weight: 600;
  border-radius: 10px;
}

.box {
  padding: 0;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  position: relative;
  /*相対配置*/
  object-fit: cover;
}

.box .sold-out {
  background-color: black;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  z-index: 10;
  position: absolute;
  /*絶対配置*/
  color: white;
  /*文字は白に*/
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  font-family: HuiFont;
}

